
.loading {
  height: 45px;
  width: 45px;
  margin: auto;
  padding: 5px;
}
.loading div.rect {
    display: inline-block;
    background-color: #333;
    width: 5px;
    height: 100%;
    margin-left: -1px;
    -webkit-animation: 1.2s infinite 0s ease-in-out strech;
            animation: 1.2s infinite 0s ease-in-out strech;
}
.loading div.rect:first-child {
      margin-left: 3px;
}
.loading div.rect-1.rect {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loading div.rect-2.rect {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
}
.loading div.rect-3.rect {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loading div.rect-4.rect {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
}
.loading div.rect-5.rect {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
@-webkit-keyframes strech {
0%, 40%, 100% {
    -webkit-transform: scaleY(0.4);
            transform: scaleY(0.4);
}
20% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
}
@keyframes strech {
0%, 40%, 100% {
    -webkit-transform: scaleY(0.4);
            transform: scaleY(0.4);
}
20% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
}



