/*
 nav configs
*/
/*
  skeleton conifgs
*/
.nav {
  top: 0px;
  position: relative;
  background: -webkit-linear-gradient(top, transparent 0%, rgba(0, 0, 0, 0.06));
  background: linear-gradient(top, transparent 0%, rgba(0, 0, 0, 0.06));
  box-shadow: 0px 3px 15px 0px #DCDCDC;
  border-radius: 0px 0px 20px 20px;
}
.nav .hideMenu {
    display: inline-block;
}
.nav .displayMenu {
    display: inline-block;
}
.nav ul {
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}
.nav li {
    display: inline-block;
    border-right: solid black 1px;
    margin-bottom: 0px;
}
.nav a {
    text-decoration: None;
    font-weight: bold;
    display: block;
    padding: 20px;
    color: #000000;
}
.nav a:hover {
    color: #33c3f0;
}
.nav a:focus {
    color: #33c3f0;
}
.nav a.router-link-active {
    color: #33c3f0;
    background: -webkit-linear-gradient(top, transparent 0%, rgba(0, 0, 0, 0.06));
    background: linear-gradient(top, transparent 0%, rgba(0, 0, 0, 0.06));
}
.nav li:first-child a.router-link-active {
    border-radius: 0px 0px 0px 20px;
}
@media (max-width: 450px) {
.nav .hamburger {
    border-radius: 5px;
    border: transparent solid 2px;
    width: 50px;
}
.nav .hamburger div {
      display: block;
      width: 40px;
      height: 5px;
      background: #000000;
      margin: 5px;
}
.nav .hideMenu {
    display: none;
}
.nav .displayMenu {
    display: block;
}
.nav li {
    padding-top: 2px;
    padding-left: 0px;
    display: block;
    border-right: none;
}
.nav li:first-child a.router-link-active {
    border-radius: 0px 0px 0px 0px;
}
.nav li:last-child a.router-link-active {
    border-radius: 0px 0px 20px 20px;
}
}



