  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clearfix, .row {
  zoom: 1;
}
.clearfix:before, .clearfix:after, .row:before, .row:after {
    content: "\0020";
    display: block;
    overflow: hidden;
    visibility: hidden;
    width: 0;
    height: 0;
}
.clearfix:after, .row:after {
    clear: both;
}
.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}
@media (min-width: 451px) {
.hidden-mobile {
    display: block !important;
}
}
@media (max-width: 450px) {
.hidden-desktop {
    display: block !important;
}
.hidden-mobile {
    display: none !important;
}
}
